import React from "react";

function Header({open}){
    
    return(
    <div className="header">
        <div>
            <a href="#" onClick={open} className="icon"><i className="fa fa-bars" aria-hidden="true"></i></a>
        </div>
        <div className="nav">
            <a href="#">FEATURES</a>
            <a href="#">DOCS</a>
            <a href="#">RESOURCES</a>
            <a href="#">EVENTS</a>
            <a href="#">BLOG</a>
        </div>
    </div>
    )}

export default Header;